import React, { Component } from 'react'
import { List, ListItem } from 'material-ui/List'

class FavoritePage extends Component {
  render() {
    return (
      <div className="page page-bg-gradient">
        { Object.keys(this.props.Parent.state.favoriteList).length ?
        <div>
          <h3> </h3>
          <List>
            { this.props.Parent.state.data.map((item, i) => this.props.Parent.state.favoriteList[item[' ']] && <ListItem
            key={`key${ i }`}
            primaryText={ item[' '] }
            secondaryText={<p className="price">.   {item['']}  {item['']}</p>} />) }
          </List>
        </div>
        : <h3> !!        !</h3> }
      </div>
    )
  }
}

export default FavoritePage


